import { Modal } from "flowbite-react";
import { useState } from "react";
import { useTimeout } from "../../utils/hooks/use_time_out";
import { useForm } from "react-hook-form";
// import { ROUTE_LINKS } from "../../utils/route_links";
import { registerFunction } from "../../api/api";
import { useMutation } from "react-query";
import { cn } from "../../utils/custom_functions";
import { FaRegEyeSlash, FaRegEye } from "react-icons/fa6";
import Terms from "../../pdf/matols.pdf";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { Button } from "../reusable_components/custom_button";
import { ThreeDots } from "react-loader-spinner";

export function RegisterModal({
  openRegisterModal,
  setOpenRegisterModal,
  setOpenSigninModal,
  ...props
}) {
  const [isPasswordVisible, setIspasswordVisible] = useState(false);
  const [isCpasswordVisible, setIsCpasswordVisible] = useState(false);
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const { set } = useTimeout(() => {
    setOpenRegisterModal(false);
    setOpenSigninModal(true);
  }, 3000);

  const {
    register,
    handleSubmit,
    watch,
    formState: { isLoading: isFormLoading, errors: formErrors },
  } = useForm();
  const {
    data,
    isLoading: isRegisteringLoading,
    isSuccess: isRegisteringSuccess,
    mutateAsync: registerAsync,
    isError: isRegisteringError,
    error: registeringError,
  } = useMutation("modal-register", {
    mutationFn: registerFunction,
    onSuccess: (data) => {
      set();
      setDisableUserInterface((p) => true);
    },
    onError: (e) => setDisableUserInterface((p) => false),
    onMutate: (data) => setDisableUserInterface((p) => true),
  });

  const onSubmit = async (data) => {
    try {
      // data["is_customer"] = true;
      await registerAsync(data);
    } catch (error) {}
    // console.log(data);
  };

  return (
    <Modal
      position={"center"}
      size={"md"}
      color="#fff"
      // className="bg-white"
      className="rounded-md"
      show={openRegisterModal}
      onClose={() => setOpenRegisterModal(false)}
    >
      <Modal.Header color="#1e293b" className="bg-slate-800">
        <span className="text-white text-2xl">Welcome</span>
      </Modal.Header>

      <Modal.Body className="bg-white" color="#fff">
        <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
          <div className="space-y-6">
            {/*Header*/}
            <div>
              <p className="text-sm font-normal text-slate-800 pt-2">
                Trust Matols logistics services to transport your items safely.
                Already have an account?{" "}
                <span
                  onClick={() => {
                    setOpenRegisterModal(false);
                    setOpenSigninModal(true);
                  }}
                  className="text-yellow-400 font-bold hover:opacity-75"
                >
                  Sign In
                </span>
                .
              </p>
            </div>
            {/*Header*/}
            {/*Fields*/}
            <div>
              <input
                {...register("email", {
                  required: "Email required",
                  disabled: disableUserInterface,
                })}
                type="email"
                name="email"
                autoComplete="email"
                placeholder="Enter email address"
                className={cn(
                  "text-sm text-slate-800  w-full relative shadow-sm block rounded-t-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                  {
                    "text-slate-800 placeholder:text-slate-800 bg-red-100":
                      formErrors.email,
                  }
                )}
              />
              <input
                {...register("user_first_name", {
                  required: "User name required",
                  disabled: disableUserInterface,
                })}
                id="user_first_name"
                type="text"
                name="user_first_name"
                placeholder="Enter first name"
                className={cn(
                  "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                  {
                    "text-slate-800 placeholder:text-slate-800 bg-red-100":
                      formErrors.user_first_name,
                  }
                )}
              />
              <input
                {...register("user_surname", {
                  required: "User surname required",
                  disabled: disableUserInterface,
                })}
                type="text"
                name="user_surname"
                placeholder="Enter surname"
                autoComplete="new-off"
                className={cn(
                  "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                  {
                    "text-slate-800 placeholder:text-slate-800 bg-red-100":
                      formErrors.user_surname,
                  }
                )}
              />
              <input
                {...register("user_contact_number", {
                  required: "User contact number required",
                  disabled: disableUserInterface,
                  maxLength: {
                    value: 10,
                    message: "Phone number cannot be more than 10 digits",
                  },
                  minLength: {
                    value: 10,
                    message: "Phone number cannot be less than 10 digits",
                  },
                })}
                type="tel"
                id="user_contact_number"
                name="user_contact_number"
                placeholder="Enter phone number"
                className={cn(
                  "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                  {
                    "text-slate-800 placeholder:text-slate-800 bg-red-100":
                      formErrors.user_contact_number,
                  }
                )}
              />
              <div className="relative">
                <input
                  {...register("password", {
                    required: "Password required",
                    disabled: disableUserInterface,
                  })}
                  type={isPasswordVisible ? "text" : "password"}
                  name="password"
                  placeholder="Enter password"
                  autoComplete="new-password"
                  className={cn(
                    "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                    {
                      "text-slate-800 placeholder:text-slate-800 bg-red-100":
                        formErrors.password,
                    }
                  )}
                />
                <div className="absolute inset-y-0 right-0 z-10 px-3 flex justify-center items-center">
                  <div onClick={() => setIspasswordVisible((r) => !r)}>
                    {!isPasswordVisible ? (
                      <FaRegEyeSlash
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    ) : (
                      <FaRegEye
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    )}
                  </div>
                </div>
              </div>
              <div className="relative">
                <input
                  {...register("cpassword", {
                    required: "Confirmation password required",
                    disabled: disableUserInterface,
                    validate: (value) => {
                      if (watch("password") !== value) {
                        return "Confirmation password does not match password";
                      }
                      return true;
                    },
                  })}
                  type={isCpasswordVisible ? "text" : "password"}
                  name="cpassword"
                  placeholder="Confirm password"
                  autoComplete="confirm-new-password"
                  className={cn(
                    "text-sm pe-9 text-slate-800 w-full shadow-sm relative block rounded-b-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                    {
                      "text-slate-800 placeholder:text-slate-800 bg-red-100":
                        formErrors.cpassword,
                    }
                  )}
                />
                <div className="absolute inset-y-0 right-0 z-10 px-3 flex justify-center items-center">
                  <div onClick={() => setIsCpasswordVisible((r) => !r)}>
                    {!isCpasswordVisible ? (
                      <FaRegEyeSlash
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    ) : (
                      <FaRegEye
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    )}
                  </div>
                </div>
              </div>
              <input
                {...register("is_customer")}
                id="is_customer"
                name="is_customer"
                type="checkbox"
                defaultChecked
                className="hidden"
              />
              <div className="flex items-center justify-start mt-4 gap-2 text-sm">
                <input
                  {...register("is_terms_cond_accept", {
                    required: "Accept our terms and privacy policy",
                    disabled: disableUserInterface,
                  })}
                  id="is_terms_cond_accept"
                  aria-label="is_terms_cond_accept"
                  name="is_terms_cond_accept"
                  type="checkbox"
                  className="rounded-sm border-gray-300 text-yellow-400 focus:ring-yellow-400 disabled:opacity-75 disabled:pointer-events-none"
                />
                <label htmlFor="termsandprivacy">
                  I agree to the{" "}
                  <a
                    href={`${Terms}`}
                    target="__blank"
                    className="font-bold text-yellow-400 hover:opacity-75"
                  >
                    Terms
                  </a>{" "}
                  and{" "}
                  <a
                    href={`${Terms}`}
                    target="__blank"
                    className="font-bold text-yellow-400 hover:opacity-75"
                  >
                    Conditions
                  </a>
                  .
                </label>
              </div>
              <div className="text-sm italic text-red-500 ">
                {[
                  formErrors.email,
                  formErrors.user_first_name,
                  formErrors.user_surname,
                  formErrors.user_contact_number,
                  formErrors.password,
                  formErrors.cpassword,
                  formErrors.is_terms_cond_accept,
                ].map((value, index) => (
                  <p key={index}>{value?.message}</p>
                ))}
              </div>
              <div>
                {isRegisteringError && (
                  <ErrorAlert
                    className="mb-2"
                    secondaryText={`${registeringError.response.data.email} ${registeringError.message}`}
                  />
                )}
                {isRegisteringSuccess && (
                  <SuccessAlert
                    secondaryText={`${data.message}. Navigate to sign in page and sigin`}
                    className="mb-2"
                  />
                )}
              </div>
            </div>
            <Button
              className="rounded-md text-base shadow-sm hover:opacity-75"
              type="submit"
              disabled={disableUserInterface}
            >
              {isRegisteringLoading || isFormLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Register new account"
              )}
            </Button>
            {/*Fields*/}
            {/**/}
          </div>
        </form>
      </Modal.Body>

      {/* <Modal.Footer></Modal.Footer> */}
    </Modal>
  );
}
