import { Modal } from "flowbite-react";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { useMutation } from "react-query";
import { useLocalStorage } from "../../utils/hooks/use_local_storage";
import { LOCAL_STORAGE_KEYS } from "../../utils/local_storage_keys";
import { useDecodeJwt } from "../../utils/hooks/use_decode_jwt";
import { useTimeout } from "../../utils/hooks/use_time_out";
import { ROUTE_LINKS } from "../../utils/route_links";
import { signInFunction } from "../../api/api";
import { cn } from "../../utils/custom_functions";
import { FaRegEyeSlash, FaRegEye } from "react-icons/fa6";
import { ThreeDots } from "react-loader-spinner";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { Button } from "../reusable_components/custom_button";

export function SigninModal({
  openSigninModal,
  setOpenSigninModal,
  setOpenRegisterModal,

  ...props
}) {
  const [isPasswordVisible, setIspasswordVisible] = useState(false);
  const { setItem } = useLocalStorage(LOCAL_STORAGE_KEYS.tokens);
  const { decodedJwt, setJwtToken } = useDecodeJwt();
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const { set } = useTimeout(() => {
    window.location.href = ROUTE_LINKS.makebooking;
  }, 3000);

  // hooks
  const {
    register,
    handleSubmit,
    formState: {
      isLoading: isFormLoading,
      errors: formErrors,
      //  isSubmitting
    },
  } = useForm();

  const {
    isLoading: isRequestLoading,
    isSuccess: isSigninSuccess,
    mutateAsync: signInAsync,
    isError: isSigninError,
    error: signinError,
  } = useMutation("modal-signin", {
    mutationFn: signInFunction,
    onSuccess: (data) => {
      setItem(data);
      setJwtToken(data.access);
      setDisableUserInterface((p) => true);
      set();
    },
    onMutate: (value) => {
      setDisableUserInterface((p) => true);
    },
    onError: (e) => {
      setDisableUserInterface((p) => false);
    },
  });

  // submit hander
  const submitHander = async (data) => {
    try {
      await signInAsync(data);
    } catch (error) {
      // console.log(error);
    }
  };

  return (
    <Modal
      id="my-modal"
      position={"center"}
      size={"md"}
      // className="bg-white"
      show={openSigninModal}
      onClose={() => setOpenSigninModal(false)}
    >
      <Modal.Header className="bg-slate-800">
        <span className="text-white text-2xl">Welcome back</span>
      </Modal.Header>
      <Modal.Body className="bg-white">
        <form className="bg-white" onSubmit={handleSubmit(submitHander)}>
          <div className="space-y-6">
            <div className="mb-6 ">
              <p className="text-sm font-normal text-slate-800">
                Trust matols logistics services to transport your items safely.
                Dont have an account ?{" "}
                <span
                  onClick={() => {
                    setOpenSigninModal(false);
                    setOpenRegisterModal(true);
                  }}
                  className="text-yellow-400 font-bold hover:opacity-75"
                >
                  Register
                </span>
                .
              </p>
            </div>

            <div>
              <input
                {...register("email", {
                  required: "Email address required",
                  disabled: disableUserInterface,
                })}
                type="email"
                name="email"
                placeholder="matols@gmail.com"
                className={cn(
                  "text-sm text-slate-800  w-full relative shadow-sm block rounded-t-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                  {
                    "text-slate-800 placeholder:text-slate-800 bg-red-100":
                      formErrors.email,
                  }
                )}
              />
              <div className="relative">
                <input
                  {...register("password", {
                    required: "Password required",
                    disabled: disableUserInterface,
                  })}
                  type={isPasswordVisible ? "text" : "password"}
                  name="password"
                  placeholder="Enter password"
                  className={cn(
                    "peer text-sm pe-9 text-slate-800 w-full shadow-sm relative block rounded-b-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-75 disabled:pointer-events-none",
                    {
                      " text-slate-800 placeholder:text-slate-800 bg-red-100":
                        formErrors.password,
                    }
                  )}
                />
                <div className="peer-focus:z-10 absolute inset-y-0 right-0 px-3 flex justify-center items-center">
                  <div onClick={() => setIspasswordVisible((r) => !r)}>
                    {!isPasswordVisible ? (
                      <FaRegEyeSlash
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    ) : (
                      <FaRegEye
                        className="text-yellow-400 cursor-pointer hover:opacity-75"
                        size={20}
                      />
                    )}
                  </div>
                </div>
              </div>
              <div>
                {[formErrors.email, formErrors.password].map((value, index) => (
                  <p key={index} className="pt-2 text-red-500 text-sm italic">
                    {value?.message}
                  </p>
                ))}
              </div>
              <div>
                {isSigninError && (
                  <ErrorAlert
                    className="mb-4"
                    secondaryText={`${signinError.response.data.message} ${signinError.message}`}
                  />
                )}
                {isSigninSuccess && (
                  <SuccessAlert
                    secondaryText={`Welcome back matols Logistics services ${decodedJwt.user_first_name} ${decodedJwt.user_surname}`}
                    className="mb-4"
                  />
                )}
              </div>
            </div>
            <Button
              className="rounded-md text-base shadow-sm hover:opacity-75"
              type="submit"
              disabled={disableUserInterface}
            >
              {isRequestLoading || isFormLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Sign in to your account"
              )}
            </Button>
          </div>
        </form>
      </Modal.Body>
    </Modal>
  );
}
