import { useMutation } from "react-query";
import { computeQuoteFunction, createBookingFunction } from "../../api/api";
import { useCreateBookingLocalStorage } from "../../utils/hooks/use_local_storage_createbooking";
import { LOCAL_STORAGE_KEYS } from "../../utils/local_storage_keys";
import {
  ErrorAlert,
  InfoAlert,
  SuccessAlert,
} from "../reusable_components/alerts/alerts";
import { useEffect, useState } from "react";
import { LoadingComponent } from "../reusable_components/loading-content";
import { BookingCard } from "../reusable_components/booking_card";
import { Button } from "../reusable_components/custom_button";
import { ThreeDots } from "react-loader-spinner";
import { ROUTE_LINKS } from "../../utils/route_links";
import { useTimeout } from "../../utils/hooks/use_time_out";
// import { useIsSignedIn } from "../../utils/hooks/use_is_signed_in";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";
import { SigninModal } from "../modals/sign_in_modal";
import { RegisterModal } from "../modals/register_modal";

export default function CreateBooking() {
  // const { isSignedIn, isStaff } = useIsSignedIn();
  const [openSigninModal, setOpenSigninModal] = useState(false);
  const [openRegisterModal, setOpenRegisterModal] = useState(false);
  const [receivedError401, setReceivedError401] = useState(false);
  const { set } = useTimeout(
    () => (window.location.href = ROUTE_LINKS.bookings),
    5000 // mili
  );
  const { _, getCreateBookingObj } = useCreateBookingLocalStorage(
    LOCAL_STORAGE_KEYS.create_booking
  );

  const {
    isLoading: isBookingBookLoading,
    isSuccess: isBookingBookSuccess,
    mutateAsync: bookingBookAsync,
    isError: isBookingBookError,
    error: bookingBookError,
  } = useMutation({
    mutationKey: "book-booking",
    mutationFn: createBookingFunction,
    onError: (e) => {
      // console.log(e, "Focus here");
      setReceivedError401(e.response.status === 401);
      setOpenSigninModal((_) => true);
    },
    onSuccess: (s) => {
      set();
    },
  });
  const {
    data: quote,
    isLoading: isComputingQuote,
    mutate: computingQuote,
    isError: isComputingQuoteError,
    error: computingQuoteError,
  } = useMutation({
    mutationKey: "compute-quote",
    mutationFn: computeQuoteFunction,
    // onSuccess: (data) => {
    //   console.log(getHtml(data));
    // },
  });

  useEffect(() => {
    const localBooking = getCreateBookingObj();
    if (localBooking) computingQuote(localBooking);
  }, []);

  const handleSubmit = async () => {
    try {
      const booking = getCreateBookingObj();
      if (booking) {
        await bookingBookAsync(booking);
      }
    } catch (error) {}
  };

  if (isComputingQuoteError) {
    return (
      <ErrorAlert secondaryText={`${computingQuoteError.response.data}`} />
    );
  }
  if (isComputingQuote) {
    return <LoadingComponent secondary_text={"Loading Quote"} />;
  }

  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto my-4 p-6 text-black bg-white">
        <h2 className="text-start text-black pb-6 font-extrabold text-3xl">
          Booking Quote
          <p className="text-sm font-normal text-slate-800 pt-2">
            Thank you for choosing Matols logistics services as your go to
            service provider.
          </p>
          <p className="text-sm font-normal text-blue-700 pt-2 ">
            If you are happy with this quote, click the book button to complete
            your booking.
          </p>
        </h2>
        {/*
         */}
        <BookingCard booking={quote} />

        <div className="flex flex-col gap-4 justify-end items-end">
          {isBookingBookError && (
            <ErrorAlert
              className="w-full"
              secondaryText={`${bookingBookError.response.data.message} ${bookingBookError.message}`}
            />
          )}
          {isBookingBookSuccess && (
            <SuccessAlert
              className="w-full"
              secondaryText={"Your booking was successfully created"}
            />
          )}
          {receivedError401 && (
            <InfoAlert
              className="w-full"
              primaryText={"Info ! Error 401"}
              secondaryText={
                "To complete a booking with Matols Logistics Services you need to be signed in. Navigate to the sign in page and sign in, other wise complete the registration process, navigate back to this page to make your booking."
              }
            />
          )}
          <div>
            <Button onClick={handleSubmit} className="w-auto  hover:opacity-75">
              {isBookingBookLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Book"
              )}
            </Button>
          </div>
        </div>
        <div>
          <SigninModal
            openSigninModal={openSigninModal}
            setOpenSigninModal={setOpenSigninModal}
            setOpenRegisterModal={setOpenRegisterModal}
          />
          <RegisterModal
            openRegisterModal={openRegisterModal}
            setOpenRegisterModal={setOpenRegisterModal}
            setOpenSigninModal={setOpenSigninModal}
          />
        </div>
        <div className="mb-20" />
      </main>
    </LoadPageAnimation>
  );
}
